@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario {
	display: flex;
	flex-direction: column;
	&__fieldset {
		border: none;
		margin-top: 1rem;
		padding: 0;
	}
	&__legend {
		color: v.$gris;
		font-size: 2.2rem;
		margin-bottom: 2rem;
	}
	&__campo {
		display: flex;
		flex-direction: column;
		gap: 1rem;
		margin: 1.5rem 0;
	}
	&__contenedor-icono {
		display: flex;
		border-radius: .5rem;
		overflow: hidden;
	}
	&__icono {
		background-color: v.$gris-oscuro;
		width: 4rem;
		display: flex;
		align-items: center;
		justify-content: center;
		i {
			color: v.$blanco;
			font-size: 2rem;
		}
	}
	&__label {
		display: block;
		font-size: 1.8rem;
		color: rgba($color: v.$negro, $alpha: .8);
	}
	&__input {
		padding: 1rem;
		border: 1px solid darken($color: v.$gris-claro, $amount: 20);
		border-radius: .5rem;
		&--file {
			border: none
		}
		&--sociales {
			@extend .formulario__input;
			flex: 1;
			border-top-left-radius: 0; 
			border-bottom-left-radius: 0; 
		}
	}
	&__radio {
		display: flex;
		gap: 2rem;
	}
	&__texto {
		margin-top: 3rem;
	}
	&__imagen {
		max-width: 20rem;
	}
	&__submit {
		@include m.boton(v.$azul, v.$blanco);
		width: auto;
		align-self: start;
		&--registrar {
			width: 100%;
		}
	}

	&__listado {
		@include m.tags;
	}
	&__tag {
		display: flex;
		gap: 1rem;
		align-items: center;
		.tag__eliminar {
			display: none;
		}
		&:hover {
			background-color: darken($color: v.$azul, $amount: 20);
			.tag__eliminar {
				display: block;
				&:hover {
					cursor: pointer;
				}
			}
		}
	}

}